import React  ,  {useEffect , useRef } from 'react';
import "./Ikons.css";
import img1 from "./Img/WhatIsRotaract_.mp4";
import {gsap} from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import logoo from "./logoo.png";
import Lottie from 'lottie-react';
import about from "./assets/animation.json";
import join from "./assets/animation2.json"
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

//smooth scroll


gsap.registerPlugin(ScrollTrigger);

export default function Ikons() {
  
  //using gsap animation 

  const img  = useRef(null);
  const textt  = useRef(null);
  const text  = useRef(null);
  const main  = useRef(null);
  const img2  = useRef(null);
  const e1  = useRef(null);
  const e2  = useRef(null);
  const e3 = useRef(null);
  const e4 = useRef(null);
  const e5  = useRef(null);
  const para  = useRef(null);
  const flip  = useRef(null);
 

  useEffect(() => {

    var tl = gsap.timeline({
      scrollTrigger:{
     trigger: textt.current,
     scroller: main.current,
    //  markers:true,
     start:"top 35%",
     end:"top 0%",
     scrub:4,
      }
    } , { scope : main})
    tl.to(textt.current, {
      x : -100,
      
    },"anim")
    tl.to(text.current , {
      x : 100,
    },"anim")
    tl.to(img.current , {
      width:"90%",
    },"anim")


    var t2 = gsap.timeline({
      scrollTrigger:{
     trigger: textt.current,
     scroller: main.current,
    //  markers:true,
     start:"top 35%",
     end:"top 0%",
     scrub:1,
      }
    } , { scope : main})
      t2.to(e1.current , {
        x: -170,
      },"anim")
      t2.to(e2.current , {
        x: 300,
      },"anim")
      t2.to(e3.current , {
        x: -150,
      },"anim")
      t2.to(e4.current , {
        y: -150,
      },"anim")
      t2.to(e5.current , {
        x: 250,
      },"anim")

      var t3 = gsap.timeline({
        scrollTrigger:{
       trigger: para.current,
       scroller: main.current,
      //  markers:true,
       start:"top 50%",
       end:"top 0%",
       scrub:4,
        }
      } , { scope : main})
      t3.to(para.current , {
        opacity:1.5, 
      },"anim")
   
  
   
},[]);
 

//card slider images 

const data =[
  {
    img:"ig/ig1.jpg",
  },
  {
    img:"ig/ig2.jpg",
  },
  {
    img:"ig/ig3.jpg",
  },
  {
    img:"ig/ig4.jpg",
  },
  {
    img:"ig/ig5.jpg",
  },
  {
    img:"ig/ig6.jpg",
  },
  {
    img:"ig/ig7.jpg",
  },
  {
    img:"ig/ig8.jpg",
  },
  {
    img:"ig/ig9.jpg",
  },
  {
    img:"ig/ig1.jpg",
  },
  {
    img:"ig/ig1.jpg",
  },
  {
    img:"ig/ig1.jpg",
  },
  {
    img:"ig/ig1.jpg",
  },
]

//card slider settings

const settings = {
  className: "center",
  centerMode: true,
  infinite: true,
  centerPadding: "60px",
  slidesToShow: 3,
  speed: 500,
  arrows:true,
};

  return (
      <>
    <div  className="main">

      {/* circle animation  */}

  <div  ref={e1} class="Ellipse1" ></div>
  <div ref={e2}class="Ellipse2"></div>
  <div ref={e3} class="Ellipse3"></div>
  <div ref={e4} class="Ellipse4"></div>
  <div ref={e5} class="Ellipse5"></div>

       {/* page 1 */}

   <div className="page1">
        <h1 className="text1" ref={textt}> Rotract</h1>
       <img className="img2" ref={img2} src={logoo} alt="" />
        <h1  className="text2" ref={text}>Ikons..</h1>
        </div>
       
       {/* page2 */}

        <div className="page2">
        {/* <img className="img1" ref={img} src={img1} alt=""  /> */}
        <video autoPlay loop muted className="img1" ref={img} src={img1}></video>
        </div> 

       {/* page3 */}


      <div className="mp">
    <div className="page3">
      <p className='about' ref={para}>
      <p className='us'>ABOUT US :</p>
      Our club is a proud community based club and over the 7 years of establishment we’ve witnessed nothing but genuine
      family-like bonds between the members, that goes on even besides rotaract. And that has been the reason of 
      motivation and success of our club- to get together and provide meaningful service in the aspect of community 
      as well as the club. Being a part of Rotaract club of Ikons, there always prevails a sense of satisfaction and 
      enjoyment through the various events planned out in the course of each Rotaract year.
      We’ve only been growing and each member takes pride in being an Ikon , in being the difference.
      </p>
    </div>
    <div className="page4">
      <Lottie animationData={about} className='animation'/>
    </div>
    </div>

    <div className="page5">
    <p className="pro">
      OUR PROJECTS
     </p>
      <img className='mm1' src="ig/General1.jpeg" alt=""/>
      <p className="meeting1">
       <b>1st General Board Meeting</b>
      </p>
      <p className='m2'>
      WE HAD OUR FIRST GENERAL BOARD MEETING ON 13/7/22. IT WAS HELD AT A CLUB MEMBER'S RESIDENCE. WE DISCUSSED ABOUT
      THE CLUB DUES AND THE VARIOUS PROJECTS WE WILL BE DOING IN NEXT TWO MONTHS. 
        <p className='info1'>
        <b>📍JASWANTH'S RESIDENCE        📅13-07-2022</b>
        </p>
      </p>
     
    
     <div className="container">
  <div className="card">
    <div className="face face1">
      <div className="content">
        <div className="icon">
          <i className="fa fa-linkedin-square" aria-hidden="true" />
        </div>
      </div>
    </div>
    <div className="face face2">
      <div className="content">
        <h3>
          <a href="#" target="_blank">
          BOOKMARK
          </a>
        </h3>
        <p>A child who reads will be an adult who thinks." Books can say a thousand things to the reader, and giving them will fill that person with multiple possibilities and forms of enjoyment.</p>
      </div>
    </div>
  </div>
  <div className="card">
    <div className="face face1">
      <div className="content">
        <div className="icon">
          <i className="fa fa-twitter-square" aria-hidden="true" />
        </div>
      </div>
    </div>
    <div className="face face2">
      <div className="content">
        <h3>
          <a href="#" target="_blank">
          Cricket Khelo 4.0
          </a>
        </h3>
        <p>
        Ikons had a fun time on a Sunday evening where all the rotaractors of Ikons had a nice sportful event of cricket where the club was divided into 4 teams and played a tournament amongst us.
        </p>
      </div>
    </div>
  </div>
  <div className="card">
    <div className="face face1">
      <div className="content">
        <div className="icon">
          <i className="fa fa-github-square" aria-hidden="true" />
        </div>
      </div>
    </div>
    <div className="face face2">
      <div className="content">
        <h3>
          <a href="#" target="_blank">
          Touch of blessings 
          </a>
        </h3>
        <p>On the ocassion of International day of older persons ,The Rotaract Club of Coimbatore Ikons visited an old age home where we interacted with members who were suffering from leprosy.</p>
      </div>
    </div>
  </div>
         
</div>
<a  href="/" className="see">See All --</a>
</div> 

<div className="page6">
   <div className="j1">
   <Lottie animationData={join} className='animation'/>
   </div>
   <div className="j2">
        <p className="joint">
          Join our vibrant community helping club , where we come together to make a diffrence!! whether you're passionate about
          volunteering , spreading kindness , or supporting thoes in need, we welcome you with open arms. Join us today and
         be a part of something biggerr...
       <b> " Be Diffrent , Be an IKON!! "</b>
        </p>
        <div className="ja"> <a href=""><button class="btn-77">Join us</button></a></div>
       
   </div>
</div>
    {/* page7 */}


    <div className="page7 ">
    <div className="board">
      <p><b>Our Board Group</b></p>
    </div>
    <div className="slider-container" >
      <Slider {...settings}>
      {data.map((d) => 
      <div>
      <img src={d.img} />
    </div>
      )}
      </Slider>
    </div>
    
      </div>

      <div className="footer">
        <div className="contact">
          <p>Contacts</p>
          <div className="con">
          <img src="ig/instagram.svg" alt="" />
          <img src="ig/facebook.png" alt="" />
          <img src="ig/mail.png" alt="" />
          </div>
        </div>
        
        <p className="copyrig">@Copyright 2024</p>
      </div>
      </div>
   </>
  );
}
